<template>
    <div class="wildfire-widget">
        <div class="left-col">
            <div class="speed-container">
                <div class="widget-slider-label">Left</div>
                <div class="speed-widget-label">{{ leituraLeft }} km/h</div>
            </div>
            <input v-model="leituraLeft" v-on:input="onLeftUpdate" @change="logInteractionData('slider', 'wind speed', $event.target.value)" type="range" min="0" max="50" value="10" step="1" class="widget-slider" id="leitura-left">
       </div>

        <div class="mid-col">
            <div class="speed-container">
                <div class="widget-slider-label">Mid</div>
                <div class="speed-widget-label">{{ leituraMid }} km/h</div>
            </div>
            <input v-model="leituraMid" v-on:input="onMidUpdate" @change="logInteractionData('slider', 'wind speed', $event.target.value)" type="range" min="0" max="50" value="10" step="1" class="widget-slider" id="leitura-mid">
        </div>

        
        <div class="right-col">
            <div class="speed-container">
                <div class="widget-slider-label">Right</div>
                <div class="speed-widget-label">{{ leituraRight }} km/h</div>
            </div>
            <input v-model="leituraRight" v-on:input="onRightUpdate" @change="logInteractionData('slider', 'wind speed', $event.target.value)" type="range" min="0" max="50" value="10" step="1" class="widget-slider" id="leitura-right">
        </div>


    </div>
</template>

<script>
import $ from 'jquery';
export default {
    name: 'wildfiresWidgetComponent',
    components:{
       
    },
    data(){
        return{
            temperature: false,
            sideView: false,
            windDirection: 90,
            windSpeed: 10,
            leituraLeft: 25,
            leituraMid: 25, 
            leituraRight: 25,           
        }
    },
    mounted(){
        this.onLeftUpdate();
        this.onMidUpdate();
        this.onRightUpdate();
    },
    methods:{
        logInteractionData(widgetType, variableName, value){
          let dataLog = {
            component_type: "widget",
            component_data: {
              widget_type: widgetType,
              variable_name: variableName,
              value: value,
            }
          }
          let logString = JSON.stringify(dataLog)
          if (window.fv && window["__USER_ID__"]) {
            window.fv.log(logString)
          }
        },
        onLeftUpdate(){
            const percent = (this.leituraLeft - ($("#leitura-left")[0].min)) / ($("#leitura-left")[0].max-$("#leitura-left")[0].min) * 100;
            $("#leitura-left").css('background','linear-gradient(to right, #FF0A01 0%, #FF0A01 ' + percent + '%, #ADADAD ' + percent + '%, #ADADAD 100%)');
        },
        onMidUpdate(){
            const percent = (this.leituraMid - ($("#leitura-mid")[0].min)) / ($("#leitura-mid")[0].max-$("#leitura-mid")[0].min) * 100;
            $("#leitura-mid").css('background','linear-gradient(to right, #FF0A01 0%, #FF0A01 ' + percent + '%, #ADADAD ' + percent + '%, #ADADAD 100%)');
        },
        onRightUpdate(){
            const percent = (this.leituraRight - ($("#leitura-right")[0].min)) / ($("#leitura-right")[0].max-$("#leitura-right")[0].min) * 100;
            $("#leitura-right").css('background','linear-gradient(to right, #FF0A01 0%, #FF0A01 ' + percent + '%, #ADADAD ' + percent + '%, #ADADAD 100%)');
        },
        tooltipText(e){
            return e.value + 'Â°'
        }

    },
    watch: {
        temperature: function(){
            this.$emit('sendValue', {type:'fv-checkbox', name:'temperature', value: this.temperature});
        },
        sideView: function(){
            this.$emit('sendValue', {type:'fv-checkbox', name:'side-view', value: this.sideView});
        },
        windSpeed: function(){
            this.$emit('sendValue', {type:'fv-slider', name:'wind-speed', value: this.windSpeed});
        },
        leituraLeft: function(){
            this.$emit('sendValue', {type:'fv-slider', name:'leitura-left', value: this.leituraLeft});
        },
        leituraMid: function(){
            this.$emit('sendValue', {type:'fv-slider', name:'leitura-mid', value: this.leituraMid});
        },
        leituraRight: function(){
            this.$emit('sendValue', {type:'fv-slider', name:'leitura-right', value: this.leituraRight});
        },
    }
}
</script>

<style scoped>
    .wildfire-widget{
        display: grid;
        grid-template-columns: minmax(0,1fr) minmax(0,1fr) minmax(0,1fr);
        grid-template-rows: 1fr;
        width: 100%;
        height: 100%;
        justify-content: space-evenly;
        align-items: center;
        font-size: 13px;
    }
    .left-col, .mid-col, .right-col{
        display: flex;
        flex-direction: column;
        height: 100%;
        justify-content: space-evenly;
        align-items: center;
    }
 
    .speed-container, .temperature-container{
        display: flex;
    }
    .speed-widget-label, .widget-checkbox-label{
        margin: 0 0.5rem;
    }
    .checkmark{
        height: 15px;
        width: 15px;
    }
    .widget-checkbox .checkmark:after {
        left: 5px;
        top: 0px;
    }

</style>
